import React, {Component} from "react";
import {connect} from "react-redux";
import PropTypes from "prop-types";
import {Expander} from "eshop/modules/core/components/ui/Expander";
import {getCartSummaryTableRows, getCartDevices, getOfferDescription, getDepositCost} from "../selectors";
import {getFeeForSelectedPaymentMethod, getSelectedDeliveryMethod} from "eshop/modules/checkout/selectors";
import SummaryDetailsTable from "./SummaryDetailsTable";
import DeliveryCostDetails from "./DeliveryCostDetails";

class SummaryDetails extends Component {

    constructor(props) {
        super(props);
    }

    componentDidMount() {
        this.loadModule();
    }

    render() {
        return (
            <div data-js-module="core/modules/expander" id={this.props.rootComponent + "-details"}>
                <div className="l-row l-row--space-large">
                    <div className="l-col l-col-12">
                        <div className="js-expander__container u-margin-top">
                            <a href="#" className="js-expander__trigger u-padding-left-s u-small-no-padding">
                                <span
                                    className="js-expander__trigger--hide">{this.props.foldedSectionTriggerLabel}</span>
                                <span
                                    className="js-expander__trigger--show">{this.props.expandedSectionTriggerLabel}</span>
                            </a>
                            <div className="js-expander__content u-hide--soft">
                                <table role="presentation" className="opl-checkout-header u-spacing-top-l">
                                    <tbody>
                                    <tr>
                                        <td className="u-no-padding-l" colSpan="2">
                                            <h3 className="h3 u-small-no-spacing-bottom">{this.props.title}</h3>
                                        </td>
                                        <td aria-hidden="true" className="with-spinbox"/>
                                        <td aria-hidden="true"
                                            className="second-last u-font-normal">{this.props.oneTimeHeader}</td>
                                        <td aria-hidden="true"
                                            className="last u-font-normal">{this.props.monthlyHeader}</td>
                                    </tr>
                                    </tbody>
                                </table>
                                <div className="o-separator o-separator--m u-medium-visible-hide u-large-visible-hide"/>
                                <Expander
                                    variant="section"
                                    className="js-stop-sticking-6"
                                    scrollToSelected={false}
                                    headerClassName="opl-section__header--with-summary"
                                    summaryClassName="opl-section__border-bottom"
                                    toggleClassName="h5 opl-checkout-section__heading g-brand1-c"
                                >
                                    {this.props.entries.map((e, i) =>
                                        <SummaryDetailsTable
                                            key={"SummaryDetailsTable_" + i}
                                            entry={e}
                                            monthlyHeader={this.props.monthlyHeader}
                                            oneTimeHeader={this.props.oneTimeHeader}
                                            id={this.props.id + "-" + i}
                                            checkoutPriceTooltipContent={this.props.checkoutPriceTooltipContent}
                                            firstMonthPriceTooltipContent={this.props.firstMonthPriceTooltipContent}/>
                                    )}
                                </Expander>
                                {this.props.deliveryMethod &&
                                <DeliveryCostDetails
                                    title={"Dostawa"}
                                    cost={this.props.deliveryMethod.price.replace(".", ",")}
                                    render={(oneTimePrice, monthlyPrice, title) => this.deliveryCustomRowView(
                                        title,
                                        this.props.oneTimeHeader,
                                        this.props.monthlyHeader,
                                        oneTimePrice, monthlyPrice)}/>
                                }
                                {this.props.feeForSelectedPaymentMethod && parseInt(this.props.feeForSelectedPaymentMethod) !== 0 &&
                                <DeliveryCostDetails
                                    title={"Opłata za płatność przy odbiorze"}
                                    cost={this.props.feeForSelectedPaymentMethod.replace(".", ",")}
                                    render={(oneTimePrice, monthlyPrice, title) => this.deliveryCustomRowView(
                                        title,
                                        this.props.oneTimeHeader,
                                        this.props.monthlyHeader,
                                        oneTimePrice, monthlyPrice)}/>
                                }
                                {this.props.deposit != null && this.props.deposit > 0 ?
                                    this.depositTableView() :
                                    null
                                }
                                <div className="u-padding-top-xl u-margin-top">
                                    <a href="#" className="js-expander__trigger u-padding-left-s u-small-no-padding">
                                        <span
                                            className="js-expander__trigger--hide">{this.props.foldedSectionTriggerLabel}</span>
                                        <span
                                            className="js-expander__trigger--show">{this.props.expandedSectionTriggerLabel}</span>
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    deliveryCustomRowView = (title, oneTimeHeader, monthlyHeader, oneTimePrice, monthlyPrice) => {
        return (
            <div className="l-row">
                <div className="l-col l-col-12">
                    <table
                        className="u-large-spacing-top u-medium-spacing-top opl-table opl-conf-summary-table opl-table--50-50 opl-table--no-border-top">
                        <caption className="u-acc-hide">{title}</caption>
                        <thead className="u-acc-hide">
                        <tr>
                            <th/>
                            <th/>
                            <th>{oneTimeHeader}</th>
                            <th>{monthlyHeader}</th>
                        </tr>
                        </thead>
                        <tbody>
                        <tr className="u-small-no-padding">
                            <td className="u-w-auto">
                                <h3 className="h5 g-brand2-c u-no-spacing">{title}</h3>
                            </td>
                            <td data-title={oneTimeHeader} className="second-last u-small-padding-l g-brand2-c">
                                {oneTimePrice}
                            </td>
                            <td data-title={monthlyHeader} className="last g-brand2-c td-null">
                                {monthlyPrice}
                            </td>
                        </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        );
    };

    depositTableView() {
        const monthlyPrice = <span>&mdash;</span>;
        const oneTimePrice = <span className="h5 g-brand2-c u-no-margin">{this.props.deposit.toFixed(2).replace(".", ",")} zł</span>;

        return (
            <div className="l-row">
                <div className="l-col l-col-12">
                    <table className="u-spacing-top-l opl-table opl-conf-summary-table opl-table--50-50 opl-table--no-border-top">
                        <caption className="u-acc-hide">Kaucja</caption>
                        <thead className="u-acc-hide">
                            <tr>
                                <th/>
                                <th/>
                                <th>{this.props.oneTimeHeader}</th>
                                <th>{this.props.monthlyHeader}</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr className="u-small-no-padding">
                                <td className="u-w-auto">
                                    <h3 className="h5 g-brand2-c u-no-spacing">Kaucja</h3>
                                </td>
                                <td data-title={this.props.oneTimeHeader} className="second-last u-small-no-padding g-brand2-c">
                                    {oneTimePrice}
                                </td>
                                <td data-title={this.props.monthlyHeader} className="last g-brand2-c td-null">
                                    {monthlyPrice}
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }

    loadModule() {
        OPL.UI.loadModules(document.getElementById(this.props.rootComponent + "-details"), [{
            path: "core/modules/expander",
            options: "",
            conditions: "",
        }]);
    }

}

SummaryDetails.propTypes = {
    rootComponent: PropTypes.string,
    expandedSectionTriggerLabel: PropTypes.string,
    foldedSectionTriggerLabel: PropTypes.string,
    feeForSelectedPaymentMethod: PropTypes.string,
    deliveryMethod: PropTypes.any,
};

SummaryDetails.defaultProps = {
    expandedSectionTriggerLabel: "Ukryj szczegóły opłat",
    foldedSectionTriggerLabel: "Pokaż szczegóły opłat",
};

const mapStateToProps = state => ({
    entries: getCartSummaryTableRows(state),
    description: getOfferDescription(state),
    devices: getCartDevices(state) || [],
    deliveryMethod: getSelectedDeliveryMethod(state),
    deposit: getDepositCost(state),
    feeForSelectedPaymentMethod: getFeeForSelectedPaymentMethod(state),
});

const mapDispatchToProps = dispatch => ({

});

export default connect(mapStateToProps, mapDispatchToProps)(SummaryDetails);
